  font-size: inherit;
  font-family: inherit;
}
.the-project-is {
  display: inline-block;
  max-width: 600px;
}
.case1,
.content8 {
  flex-direction: row;
  flex-wrap: wrap;
}
.content8 {
  flex: 1;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  box-sizing: border-box;
  display: flex;
  padding: 30px 20px;
  align-items: center;
  justify-content: space-between;
  min-width: 400px;
  max-width: 1160px;
}
.case1 {
  align-self: stretch;
  overflow: hidden;
  justify-content: center;
  min-width: 380px;
  cursor: pointer;
}
.button4,
.case1,
.cases2,
.content11 {
  display: flex;
  align-items: center;
}
.content11 {
  flex: 1;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 30px 20px;
  box-sizing: border-box;
  justify-content: space-between;
  min-width: 400px;
  max-width: 1160px;
}
.button4,
.cases2 {
  overflow: hidden;
  justify-content: center;
  cursor: pointer;
}
.button4 {
  border: 0;
  padding: 14px 28px;
  background-color: #3936eb;
  border-radius: 5px;
  box-shadow: 0 70px 20px transparent, 0 45px 18px rgba(0, 0, 0, 0.01),
    0 25px 15px rgba(0, 0, 0, 0.05), 0 11px 11px rgba(0, 0, 0, 0.09),
    0 3px 6px rgba(0, 0, 0, 0.1), 0 0 0 rgba(0, 0, 0, 0.1);
  width: 363px;
  height: 80px;
  flex-shrink: 0;
  flex-direction: row;
  box-sizing: border-box;
  min-width: 300px;
  max-width: 363px;
}
.button4:hover {
  background-color: #c0c0ff;
}
.cases2 {
  align-self: stretch;
  background-color: #201dc4;
  flex-direction: column;
  padding: 150px 0;
  gap: 50px;
  text-align: center;
  font-size: 40px;
  color: #fff;
  font-family: Poppins;
}
.logo-icon1 {
  position: relative;
  width: 177px;
  height: 52px;
  overflow: hidden;
  flex-shrink: 0;
}
.content12 {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 10px;
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
  min-width: 150px;
}
.partners4 {
  position: relative;
  letter-spacing: -0.02em;
  font-weight: 500;
}
.partners4:hover {
  color: rgba(255, 255, 255, 0.5);
}
.desktop-1440,
.footer,
.header-menu1 {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header-menu1 {
  width: 100%;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 20px;
  box-sizing: border-box;
  gap: 20px;
  opacity: 0.5;
  min-width: 300px;
  max-width: 700px;
}
.desktop-1440,
.footer {
  flex-direction: column;
  color: #fff;
  font-family: Poppins;
}
.footer {
  align-self: stretch;
  background-color: #1b1b1b;
  padding: 100px;
  gap: 40px;
  text-align: left;
  font-size: 16px;
}
.desktop-1440 {
  position: relative;
  background-color: #fff;
  width: 100%;
  text-align: center;
  font-size: 30px;
}
@media screen and (max-width: 420px) {
  .in-strong-reputation {
    font-size: 30px;
  }
  .hero {
    gap: 70px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 70px;
    box-sizing: border-box;
  }
  .blog1,
  .media4,
  .partners2,
  .services2,
  .services3,
  .team2 {
    padding-top: 70px;
    padding-bottom: 70px;
    box-sizing: border-box;
  }
  .vector-icon {
    display: none;
  }
  .the-project-is {
    font-family: Poppins;
    font-size: 15px;
  }
  .cases2,
  .footer {
    padding-top: 70px;
    padding-bottom: 70px;
    box-sizing: border-box;
  }
}
